.select-container{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.select-background{
    margin: 0;
    width: 100%;
    height: 600vh;
    background-image: linear-gradient(#910289,#439acc, #30b85d, #9e1c2d, #910289);
    animation: gradient-slide 10s ease-in-out infinite;
    position: absolute;
    z-index: -1;
}

.backButton{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 200px;
    height: 100px;
    background-color: gray;
    border-radius: 10px;
    top: 10px;
    left: 10px;
    cursor: pointer;
}

.backButton p {
    font-size: 16px;
    font-weight: bold;
}

.chestContainer{
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
}

@keyframes gradient-slide {
    0% {
        translate: 0 0;
    }
    50% {
        background-position: 0 300vh;
    }
    100% {
        background-position: 0 600vh;
    }
}