.footer-box{
    width: 100%;
    display: flex;
    justify-content: end;
    flex-direction: column;
    align-items: end;
    position: absolute;
    bottom: 0;
}

.footer-container{
    width: 100%;
    background-color: rgb(22, 22, 22);
    height: 50px;
}

.footer-trees-container{
    display: flex;
    justify-content: center;
    flex-direction: row;
    position: relative;
}

@media screen and (max-width:1000px){
    .footer-container{
        width: 100%;
    }
}