.mountainContainer{
    display: flex;
    position: absolute;
    align-items: flex-end;
    width: 100%;
    height: 25vh;
    bottom: 50px;
}

.mountain:nth-of-type(1){
    position: relative;
    left: 5%;
    width: 90%;
    height: 22vh;
    z-index: 2;
    background-image: linear-gradient(#8ad3cf, #414141ab 40%);
    clip-path: polygon(49% 0, 51% 0, 100% 100%, 0% 100%);
}

.mountain:nth-of-type(2){
    position: relative;
    width: 100%;
    height: 25vh;
    z-index: 1;
    background-image: linear-gradient(#cfebe9, #74757528 40%);
    clip-path: polygon(49% 0, 51% 0, 100% 100%, 0% 100%);
}

.mountain:nth-of-type(3){
    position:relative;
    right: 5%;
    width: 70%;
    height: 15vh;
    z-index: 2;
    background-image: linear-gradient(#55d3cd, #353535b2 40%);
    clip-path: polygon(49% 0, 51% 0, 100% 100%, 0% 100%);
}

.mountain:nth-of-type(4){
    position:relative;
    width: 70%;
    height: 15vh;
    z-index: 3;
    background-image: linear-gradient(#55d3cd, #353535b2 40%);
    clip-path: polygon(48% 0, 52% 0, 100% 100%, 0% 100%);
}
.mountain:nth-of-type(5){
    position:relative;
    width: 80%;
    right: 3%;
    height: 15vh;
    z-index: 3;
    background-image: linear-gradient(#55d3cd, #272727 40%);
    clip-path: polygon(48% 0, 52% 0, 100% 100%, 0% 100%);
}