.chest-box{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 125px;
    height: 100px;
    background-color: brown;
    border: 5px solid rgb(196, 233, 34);
    border-radius: 25px 25px 0 0;
    cursor: pointer;
}

.middle-box{
    margin-bottom: 10px;
    width: 100%;
    height: 10px;
    background-color: rgb(196, 233, 34);
}

.keyhole{
    position: absolute;
    translate: 0 -7px;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background-color: black;
}

.keyhole::before{
    content:'';
    position: relative;
    bottom: 10px;
    border-left:10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 20px solid black;
}